.table {
  border-collapse: collapse;
  width: 80%;
}

.th, td {
  text-align: left;
  padding: 8px;
}

.tr:nth-child(even) {background-color: #f2f2f2;}

.button {
background-color: #ffaa3b;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
.bodyPage{
   background-color: #274e7d;
}